<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="filters">
        <el-form-item>
          <el-input v-model="filters.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" v-on:click="loadData"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button size="primary" @click="handleAdd"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!---->
    <el-table
      :data="aData"
      class="tb-edit"
      style="width: 100%"
      highlight-current-row
      height="550"
      border
    >
      <el-table-column label="" align="center" width="180">
        <el-table-column type="index" width="80" ></el-table-column>
        <el-table-column label="id" width="180">
          <template scope="scope">
            <span>{{scope.row.id}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="180">
          <template scope="scope">
            <el-input
              size="small"
              v-model="scope.row.companyCode"
              placeholder=""
              @change="handleEdit(scope.$index, scope.row)"
            ></el-input>
            <span>{{scope.row.companyCode}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="180">
          <template scope="scope">
            <el-input
              size="small"
              v-model="scope.row.companyName"
              placeholder=""
              @change="handleEdit(scope.$index, scope.row)"
            ></el-input>
            <span>{{scope.row.companyName}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="180">
          <template scope="scope">
            <el-input
              size="small"
              v-model="scope.row.code"
              placeholder=""
              @change="handleEdit(scope.$index, scope.row)"
            ></el-input>
            <span>{{scope.row.code}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="180">
          <template scope="scope">
            <el-input
              size="small"
              v-model="scope.row.name"
              placeholder=""
              @change="handleEdit(scope.$index, scope.row)"
            ></el-input>
            <span>{{scope.row.name}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="180">
          <template scope="scope">
			  <el-select v-model="scope.row.jsfs" placeholder="">
      			<el-option label="" value="" ></el-option>
      			<el-option label=""  value="" ></el-option>
      			<el-option label="" value=""  ></el-option>
    		</el-select>
            <span>{{scope.row.jsfs}}</span>
          </template>
        </el-table-column>

        <el-table-column label="" width="180">
          <template scope="scope">
			  <el-select v-model="scope.row.sfjs" placeholder="">
      			<el-option label="" value="" ></el-option>
      			<el-option label=""  value="" ></el-option>
    		</el-select>
            <span>{{scope.row.sfjs}}</span>
          </template>
        </el-table-column>

        <el-table-column label="" width="180">
          <template scope="scope">
            	  <el-select v-model="scope.row.sfhs" placeholder="">
      			<el-option label="" value="" ></el-option>
      			<el-option label=""  value="" ></el-option>
    		</el-select>
            <span>{{scope.row.sfhs}}</span>
          </template>
        </el-table-column>

        <el-table-column label="" width="180">
          <template scope="scope">
               <el-select v-model="scope.row.taxRate" placeholder="">
      			<el-option label="0%" value="0%" ></el-option>
      			<el-option label="3%" value="3%" ></el-option>
      			<el-option label="6%" value="6%" ></el-option>
      			<el-option label="10%" value="10%" ></el-option>
      			<el-option label="16%" value="16%" ></el-option>
    		</el-select>
            <span>{{scope.row.taxRate}}</span>
          </template>
        </el-table-column>

        <el-table-column label="" width="180">
          <template scope="scope">
              <el-select v-model="scope.row.sfjzjt" placeholder="">
      			<el-option label="" value="" ></el-option>
      			<el-option label=""  value="" ></el-option>
    		</el-select>
            <span>{{scope.row.sfjzjt}}</span>
          </template>
        </el-table-column>

        <el-table-column label="" width="180">
          <template scope="scope">
            <el-input
              size="small"
              v-model="scope.row.kpgz"
              placeholder=""
              @change="handleEdit(scope.$index, scope.row)"
            ></el-input>
            <span>{{scope.row.kpgz}}</span>
          </template>
        </el-table-column>

        <el-table-column label="" width="180">
          <template scope="scope">
            <el-input
              size="small"
              v-model="scope.row.goodsCode"
              placeholder=""
              @change="handleEdit(scope.$index, scope.row)"
            ></el-input>
            <span>{{scope.row.goodsCode}}</span>
          </template>
        </el-table-column>

        <el-table-column label="" width="180">
          <template scope="scope">
            <el-input
              size="small"
              v-model="scope.row.goodsName"
              placeholder=""
              @change="handleEdit(scope.$index, scope.row)"
            ></el-input>
            <span>{{scope.row.goodsName}}</span>
          </template>
        </el-table-column>

        <el-table-column label="" width="180">
          <template scope="scope">
            <el-input
              size="small"
              v-model="scope.row.rowIndex"
              placeholder=""
              @change="handleEdit(scope.$index, scope.row)"
            ></el-input>
            <span>{{scope.row.rowIndex}}</span>
          </template>
        </el-table-column>

        <el-table-column label="" width="180">
          <template scope="scope">
            <el-input
              size="small"
              v-model="scope.row.tableName"
              placeholder=""
              @change="handleEdit(scope.$index, scope.row)"
            ></el-input>
            <span>{{scope.row.tableName}}</span>
          </template>
        </el-table-column>

        <el-table-column label="" width="180">
          <template scope="scope">
            <el-input
              size="small"
              v-model="scope.row.isEnable"
              placeholder=""
              @change="handleEdit(scope.$index, scope.row)"
            ></el-input>
            <span>{{scope.row.isEnable}}</span>
          </template>
        </el-table-column>
      </el-table-column>
	   <el-table-column label="" fixed="right">
          <template scope="scope">
            <el-button size="small" type="danger" @click="handleDel(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
    </el-table>
  </section>
</template>

<script>
import util from "../../common/js/util";
//import NProgress from 'nprogress'
import { getA, removeA, addA, editA } from "../../api/api";

export default {
  data() {
    return {
      aData: [],
      filters: {
        name: ""
      },
      users: [],
      total: 0,
      page: 1,
      listLoading: false,
      editLoading: false
    };
  },
  methods: {
    //
    loadData() {
      let para = {
        page: this.page,
        name: this.filters.name
      };
      this.listLoading = true;
      //NProgress.start();
      getA(para).then(res => {
        this.total = res.data.total;
        this.aData = res.data.list;
        this.listLoading = false;
      });
    },
    //
    handleDel: function(index, row) {
      this.$confirm("?", "", {
        type: "warning"
      })
        .then(() => {
          this.listLoading = true;
          //NProgress.start();
          let para = { id: row.id };
          removeA(para).then(res => {
            this.listLoading = false;
            //NProgress.done();
            this.$message({
              message: "",
              type: "success"
            });
            this.loadData();
          });
        })
        .catch(() => {});
    },
    handleAdd: function() {}
  },
  mounted() {
    this.loadData();
  }
};
</script>

<style scoped>
.tb-edit .el-input {
  display: none;
}
.tb-edit .el-select {
  display: none;
}
.tb-edit .current-row .el-input {
  display: block;
}
.tb-edit .current-row .el-select {
  display: block;
}
.tb-edit .current-row .el-input + span {
  display: none;
}
.tb-edit .current-row .el-select + span {
  display: none;
}

</style>


