<template>
  <div class="mainTableContainer">
    <el-table :data="mainTable" style="width: 100%" :span-method="objectSpanMethod" border>
      <el-table-column label="       " align="center">
        <el-table-column label-class-name="font12" label="" header-align="center">
          <el-table-column
            header-align="left"
            label-class-name="font10 color999"
            label="    "
          >
            <el-table-column label="     " header-align="center">
              <el-table-column width="40px">
                <template slot-scope="scope">
                  <div
                    style="width: 14px;font-size:14px;word-wrap:break-word;letter-spacing:20px; "
                  >{{ scope.row.itemType }}</div>
                </template>
              </el-table-column>

              <el-table-column>
                <template slot-scope="scope">
                  <el-popover
                    v-if="scope.row.titles&&scope.row.titles.length>0"
                    trigger="hover"
                    placement="right"
                  >
                    <div>
                      <p v-for="title in  scope.row.titles" :key="title">{{title}}</p>
                    </div>
                    <div slot="reference">
                      <el-badge is-dot class="hasTitle">{{ scope.row.item }}</el-badge>
                    </div>
                  </el-popover>
                  <div v-else style="white-space:pre;">{{ scope.row.item }}</div>
                </template>
              </el-table-column>
            </el-table-column>
            <el-table-column label="" header-align="center"></el-table-column>
            <el-table-column label="" header-align="center">
              <el-table-column label="" header-align="center"></el-table-column>
              <el-table-column label="" header-align="center"></el-table-column>
            </el-table-column>
            <el-table-column label="" header-align="center">
              <el-table-column label="" header-align="center"></el-table-column>
              <el-table-column label="" header-align="center"></el-table-column>
            </el-table-column>
          </el-table-column>
        </el-table-column>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      mainTable: [
        {
          itemType: "",
          item: "",
          titles: [
            `1 `,
            ``,
            ` `,
            ``,
            ``,
            ``,
            `231`
          ]
        },
        {
          item: "",
          titles: [
            ``,
            ``
          ]
        },
        {
          item: "           "
        },
        {
          item: "           "
        },
        {
          item: ""
        },
        {
          item: ""
        },
        {
          item: ""
        },
        {
          item: ""
        },
        {
          item: ""
        },
        {
          item: "           "
        },
        {
          itemType: "",
          item: ""
        },
        {
          item: ""
        },
        {
          item: ""
        },
        {
          item: ""
        },
        {
          item: ""
        },
        {
          item: ""
        },
        {
          item: ""
        },
        {
          item: ""
        },
        {
          item: ""
        },
        {
          item: ""
        },
        {
          item: ""
        },
        {
          item: ""
        },
        {
          item: ""
        },
        {
          item: ""
        },
        {
          itemType: "",
          item: ""
        },
        { item: "" },
        { item: "" },
        { item: "" },
        { item: "" },
        { item: "" },
        { item: "" },
        { item: "" },
        { item: "0" },
        { item: "()" },
        { item: "" },
        { item: "" },
        { item: "" },
        { item: "" },
        {
          itemType: "",
          item: "      "
        },
        {
          item:
            "                                                                      "
        },
        {
          item:
            "                                             "
        },
        { item: "" },
        { item: "" },
        {
          item:
            "                                                                                "
        }
      ]
    };
  },
  methods: {
    deleteRow(index, rows) {
      rows.splice(index, 1);
    },
    objectSpanMethod({ row, column, rowIndex, columnIndex }) {
      debugger;
      if (columnIndex == 0) {
        if (rowIndex == 0) {
          return {
            rowspan: 10,
            colspan: 1
          };
        } else if (rowIndex == 10) {
          return {
            rowspan: 14,
            colspan: 1
          };
        } else if (rowIndex == 24) {
          return {
            rowspan: 14,
            colspan: 1
          };
        } else if (rowIndex == 38) {
          return {
            rowspan: 7,
            colspan: 1
          };
        } else {
          return {
            rowspan: 0,
            colspan: 0
          };
        }
      }
      if (columnIndex == 1) {
        switch (rowIndex) {
          case 38:
          case 39:
          case 40:
          case 41:
          case 42:
          case 43:
            return {
              rowspan: 1,
              colspan: 3
            };
        }
      }

    }
  }
};
</script>
<style type="css" >
.font12 {
  font-size: 12px;
}
.font10 {
  font-size: 10px;
  color: #999;
}
.el-table__footer-wrapper thead div,
.el-table__header-wrapper thead div.color999 {
  color: #999;
}
.hasTitle {
  margin-top: 10px;
  margin-right: 40px;
}
</style>




