        iconCls: 'fa fa-id-card-o',
        children: [
            { path: '/qq', component: Page4, name: '' } ,
            { path: '/ww', component: Page4, name: '' } ,
            { path: '/ee', component: Page4, name: '' } ,
            { path: '/rr', component: Page4, name: '' } ,
            { path: '/tt', component: Page4, name: '' } ,
            { path: '/yy', component: Page4, name: '' } ,
        ]
